.display {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
//justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
}

.component-display {
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 54%;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

.component-display div {
  background-color: #FFFFFF;
  border: 0;
  font-size: 1rem;
  margin: 0 1px 0 0;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  padding: 0;
}

.component-display.wide {
  width: 100%;
}

.component-display.orange div {
  background-color: #f5923e;
  color: white;
}

/*
[class > element] Selects all <div> elements where the parent is .component-display class
*/

.number {
  font-size: 0.8em;
}

.symbol {
  font-size: 1.0em;
}

.name {
  font-size: 0.5em;
}

.weight {
  font-size: 0.5em;
}

.message {
  font-size: 2.0em;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 100%;
}

.table {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background-color: #858694;
  width: 100%;
}


